@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,400;0,700;1,400&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');


body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Lato', sans-serif;
  color:rgb(3, 3, 71);
}

h1, h2, h3 {
  text-align: center;
}


/*  ~~~~~~~~~~~~~~~~ HERO IMAGE ~~~~~~~~~~~~~~~~ */

#topBarContainer {
  z-index:999;
}


#topBar {
  color:rgb(230, 230, 230);
  z-index:999;
  height:8%;
  margin-top:5%;
  display:flex;
  flex-direction: column;
}

#topBar h2 {
  padding-left:10px;
}

#topBar p {
  text-align:center;
  margin:0;
  font-size: 3vh;
}

#logoDiv {
  display:flex;
  flex-direction:column;
  align-items:center;
}

#logoDiv h2 {
  font-size:4vh;
  padding:0;
}

#logo{
  border-radius:50%;
  width:15vw;
}

#firstText {
  z-index:999;
  display:flex;
  flex-direction:column;
  justify-content: center;
  height:100vh;
}


#firstText h1, #firstText h2 {  
  margin: 2%;
}

#firstText h1 {
  color:rgb(255, 255, 255);
  font-size:5vh;
  
}

#firstText h2{
  color:rgb(206, 206, 206);
  font-size:4vh;
}

#firstText h3 {
  font-size:4vh;
  margin:5px;
}

#firstButton {
  margin-top:7%;
  align-self:center;
  font-size: 3vh;
}

#firstContainer {
  font-family: 'Lato', sans-serif;
  display:flex;
  flex-direction:column;
  height:100vh;
  background-image: url(images/manProdPhoto3.jpg);
  background-size:cover;
  background-position-y: 20%;
  background-position-x: 50%;
}

#firstCompanyDetails {
  display:none;
}

.overlay {
  background: linear-gradient(to right, #2d9aa8, #711e72);
  opacity: 0.7;
  position: absolute;
}

#firstOverlay {
  height:100vh;
  width:100%;
}


button, input[type="submit"]{
  display:flex;
  align-items:center;
  justify-content: center;
  border-radius:8px;
  padding:8px;
  margin-right:2%;
  cursor:pointer;
}

.blueBtn {
  background-color: rgb(23, 23, 126);
  color:rgb(247, 247, 247);
  border:none;
}

.blueBtn a {
  color:rgb(247, 247, 247);
  text-decoration: none;
}

.blueBtn:hover {
  background-color: rgb(33, 33, 182);
  color:white;
}

.centerBtn {
    justify-content: center;
    display: flex;
    padding:10px;
}

.centerBtn button {
  margin-right:5px;
}

.bigBtn {
  font-size:4vh;
}

.formDiv  {
  margin-bottom:0;
  padding:0;
}

/* ~~~~~~~~~~~~~~~~ PRODUCT INFO / BENEFITS ~~~~~~~~~~~~~~~~ */

#productContainer {
    height:100vh;
}

#productContainer h1 {
  padding-top:5%;
  font-size:4vh;
  text-align:center;
  margin:0;
}

#productContainer h3 {
  font-size:2vh;
  text-align:center;
}

.maskDiv {
    padding-top:5%;
    display:flex;
    flex-direction:column;
    align-items:center;
}
#maskPic {
  width:30vh;
}
.infoCardDiv {
  padding-top: 5%;
  width:40%;
  font-size:100%;
  display:flex;
  flex-direction:column;
  align-items: center;
  text-align:left;
  margin-left:1%;
  margin-right:1%;
  font-size:1.8vh;
}

.infoCardDiv h3{
  margin:1%;
}
#infoCards {
  display:flex;
  flex-direction: column;
  justify-content: center;
}

#infoCards{
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  margin:0;
  padding:0;
}

.infoIcon {
  width:5vh;
  padding-bottom:10px;
}

/* ~~~~~~~~~~~~~~~~ INTERACTIVE MAP / SOCIAL PROOF ~~~~~~~~~~~~~~~~ */
#mapContainer {
  display:flex;
  position:relative;
  flex-direction: column;
  font-size:0.8em;
}

#worldMap {
  display:flex;
  width:90vw;
  align-self:center;
}

#mapDiv {
  display:flex;
  flex-direction: column;
}

#mapOverlay {
  background: linear-gradient(to right, #ce09ad, #ece91b);
  opacity: 0.4;
  position: absolute;
  height: 100%;
  width: 100%;
}


#mapTopBar {
  display:flex;
  flex-direction: column;
  z-index:999;

  
}
#mapTopBar h1 {
  font-size:2em;
  margin-top:2vh;
  margin-bottom:2vh;
}

#mapTopBar h3 {
  font-size:1em;
  margin:1px;
  margin-bottom: 3%;
}


.credit {
  width:100%;
  display:flex;
  justify-content: center;
  padding-bottom:10px;
  z-index:999;
}
.credit a{
  padding-right:10px;
}

.mapButton {
  position:absolute;
  font-size:1.5em;
}

.button:hover {
  cursor:pointer;
}

.hideButtonDiv {
  display:flex;
  justify-content: flex-end;
}

.hideButton {
  font-size:3.4vh;
  background:none;
  border:none;
  cursor:pointer;
}

.hidden {
  display:none;
}

.mapStudy {
  position:absolute;
  background-color:white;
  height: 44vh;
  width:88%;
  padding:5%;
  top: 102%;
  left:1%;
}

.mapStudy table {
  padding-top: 15px;
}

#study_1{
  font-size: 3vh;
}

#study_1_btn { 
  top:34%;
  left:11%;
}

#study_2{
  font-size: 2.4vh;
  
}  

#study_2_btn {
  top: 50%;
  left:13%;
}

#study_3{
  font-size: 2.6vh;
  
} 

#study_3_btn {
  top:44%;
  left:45%;
}

#study_4{
  font-size: 2.4vh;
  
}

#study_4_btn {
  top:30%;
  right:22%;
}


#study_5{
  font-size: 2.4vh;
  
} 

#study_5_btn {
  top:58%;
  right:22%;
}

#study_6{
  font-size: 2.6vh;
  
}

#study_6_btn {
  top:62%;
  right:3%;
}


#mapBox {
  background-color: rgb(29, 29, 29);
  color: white;
  height: 54vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size:2.5vh;
}

#mapBox h3 {
  width:46%;
}

/* ~~~~~~~~~~~~~~~~ CONTACT FORM / CALL TO ACTION ~~~~~~~~~~~~~~~~ */

#contact {
  display:flex;
  background-image: url("images/factorypic3.jpg");
  background-size:cover;
  flex-direction: column;
  height:100vh;
 }

 #contactOverlay {
  height:100vh;
  width:100%;
}
 #contactContainer {
  display:flex;
  flex-direction: column;
  align-items:center;
  justify-content: space-evenly;
  z-index:999;
 }

.contactOption {
  font-size: 2.8vh;
  background-color: white;
  padding: 9px;
  text-align: center;
  width: 70%;
  align-items: center;
  display: flex;
  flex-direction: column;
}

#contactLogoDiv {
  display:none;
}

#contactHeader {
  text-align: center;
  background-color: rgb(23, 23, 126);
  color:rgb(247, 247, 247);
  z-index:999;
  display:flex;
  justify-content: center;
}

.contactBorder {
  border: black 1px solid;
  border-radius:8px;
}

#phoneIcon {
  width:4vh;
  padding-right:10px;
}

.cursorBtn {
  cursor:pointer;
}

/* FORM MODAL */

.modalContainer {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top:0;
  left:0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalShadow {
  position:fixed;
  opacity:0.4;
  background-color:black;
  width:100vh;
  height:100vh;
  
}

#contactForm{
  margin:2%;
  display:flex;
  flex-direction: column;
  width:90%;
  font-size: 0.8em;
  background-color:white;
  z-index:999;
}

#contactForm input[type="text"], #contactForm input[type="text"], #contactForm input[type="phone"], #contactForm input[type="email"],  #contactForm input[type="url"] {
  width:80%;
}

#contactBooking {
  z-index:999;
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
  padding:10px;
}

#bookTitle {
  display:flex;
  justify-content: center;
  width:100%;
}

.contactOption p {
  margin:3vh;
}

.formDiv {
  padding:5px;
  display:flex;
  justify-content: space-around;
}

.formDiv p {
  padding-left: 10px;
  padding-right: 10px;
}

.formField {
  display:flex;
  flex-direction:column;
  align-items:center;
}

.formField label{
  padding-bottom:5px;
}

.formField input[type="submit"] {
  font-family: 'Lato', sans-serif;
}



#meetingPic {
  align-self:center;
  width:500px;
  height: 300px;
}

#meetingInput {
  display:flex;
  justify-content: center;
}

#meetingInput div {
  margin:3%;
}

#cancelBtn {
  margin:5px;
}

#xBtn {
  font-size:3vh;
}



/* ~~~~~~~~~~~~~~~~ MEDIA QUERIES  ~~~~~~~~~~~~~~~~ */

@media (min-width:480px){
  #topBar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:2%;
    margin-top:0;
  }
  
  #logoDiv {
    flex-direction: row;
    font-size:2.7vh;
  }

  #logo {
    width: 7vw;
    margin-right: 5%;
  }
  
  #firstCompanyDetails {
    display: flex;
  }

  #topBar p {
    margin: 0;
    font-size: 2.7vh;
  }

  /* Product Info */
  #maskPic {
    width:55vh;
  }

  
  .infoIcon {
    width: 8vh;
  }
  #productContainer {
    height:auto;
    
  }

  #productContainer h1{
    padding-top: 2%;
    font-size: 4.8vh;
  }

  #productContainer h3 {
    font-size: 3vh;
  }

  .infoCardDiv p {
    font-size: 2.7vh;
  }
  #mapBox {
    display:none;
  }

  #study_1{
    top:34%;
    left:11%;
    font-size: 3vh;
  }
  
  .mapStudy {
    border-radius:8px;
    border: black 1px solid;
    height:fit-content;
    width: 60%;
    padding: 2%;
    font-size: 2.6vh;
    cursor:pointer;
  }

  .mapStudy table{
    padding-top:0;
  }

  .hideButton {
    position:absolute;
    display: flex;
    justify-content: flex-end;
    background:none;
    color:black;
  }

  #study_1_btn { 
    top:34%;
    left:11%;
  }
  
  #study_2{
    top: 40%;
    left:13%;
  }  
  
  #study_2_btn {
    top: 50%;
    left:13%;
  }
  
  #study_3{
    top:44%;
    left:25%;
  } 
  
  #study_3_btn {
    top:44%;
    left:45%;
  }
  
  #study_4{
    top: 19%;
    left: 27%;
  }
  
  #study_4_btn {
    top: 30%;
    right: 32%;
  }
  
  
  #study_5{
    top: 58%;
    left: 21%;
  } 
  
  #study_5_btn {
    right: 20%;
    top: 58%;
  }
  
  #study_6{
    top:61%;
    left:34%;

  }
  
  #study_6_btn {
    right: 10%;
    top: 62%;
  }
  
  #contactForm {
    font-size:1em;
    width:fit-content;
  }
  .contactOption {
    margin: 2vh;
    width:45%;
  }
  .contactOption p {
    margin: 3vh;
  }
  .contactOption button, .contactOption input  {
    font-size:2.7vh;
    
  }
  
  #meetingInput div {
    margin: 1%;
  }
  .modalShadow{
    width: 120%;
  }

  @media (min-width:670px){
    #book {
      margin-top:2vh;
    }
    .contactOption {
      margin: 2vh;
    }
    #worldMap {
      display:flex;
      width:55vw;
      align-self:center;
    }
    #logo {
      width: 4vw;
      margin-right: 5%;
    }

    .infoIcon {
      width:10vh;
      padding-bottom:5px;
    }

    .mapStudy {
    width: auto;
    padding:1%;
   
    }

    #study_1,  #study_2,  #study_3,  #study_4,  #study_5,  #study_6 {
      font-size:1.5em;
    }

    #study_1_btn {
      left: 28%;
    }

    #study_2_btn {
      top: 48%;
      left: 29%;
    }
    
    #study_5 {
      left: 40%;
    }

    #study_5_btn {
      right: 30%;
    }
    #study_6 {
      right: 23%;
      top: 50%;
    }

    #study_6_btn {
      right: 23%;
    }

    .contactOption {
      width: 20%;
    }
    #infoCards {
      padding-bottom:2%;
    }


  } 
}
